//to ignore cli warning about deprecated mixin
$enable-deprecation-messages: false;
$ignore-warning : true;

// Custom class not bootstrap
$fn: 'HelveticaNeue'; // fn = font normal 
$fm: 'HelveticaNeueMed'; // fm = font Medium 
$border-radius: 0.5rem;
// Body
$body-bg : #E6EBEE;
$body-color: #717579;
$headings-color: #424562;
$b-color: #DDDDDD;
$main-color: #2A353A;
$bg-color: #ffffff;
$body-text: #666666;
$border-light: #D5DFE7;


// Typography
$font-family-base: 'Poppins', sans-serif;
$headings-font-weight : 600;
// $headings-font-family:        $fm;

$font-size-base : 0.875rem !default;
$font-weight-base : 400 !default;
// $line-height-base: 1.6;
$h1-font-size : 2.25rem;
$h2-font-size : 1.875rem;
$h3-font-size : 1.5rem;
$h4-font-size : 1.125rem;
$h5-font-size : 1rem;
$h6-font-size : 0.938rem;


//
// Color system
//
$white : #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black : #000 !default;


$blue : #374C98;
$indigo: #6610f2 !default;
$purple: #6f42c1 !default;
$pink : #EB62D0 !default;
$red : #EE3232;
$orange: #ff9900 !default;
$yellow: #FFFA6F;
$green : #297F00;
$teal : #20c997 !default;
$cyan : #3065D0;



$grays: (
  ) !default;
// stylelint-disable-next-line scss/dollar-variable-default
$grays: map-merge(("100": $gray-100,
      "200": $gray-200,
      "300": $gray-300,
      "400": $gray-400,
      "500": $gray-500,
      "600": $gray-600,
      "700": $gray-700,
      "800": $gray-800,
      "900": $gray-900),
    $grays
);


$muted : #89879f;
$text-muted: #89879f;
$mine-shaft: #262626;
$ebony-clay: #232833;

$colors: (
  ) !default;
// stylelint-disable-next-line scss/dollar-variable-default
$colors: map-merge(("blue": $blue,
      "indigo": $indigo,
      "purple": $purple,
      "pink": $pink,
      "red": $red,
      "orange": $orange,
      "yellow": $yellow,
      "green": $green,
      "teal": $teal,
      "cyan": $cyan,
      "white": $white,
      "gray": $gray-600,
      "gray-dark": $gray-800),
    $colors
);



$primary : #ffa722db !default;
$secondary: #9568FF !default;
$success : #1EBA62 !default;
$info : #00ADA3 !default;
$warning : #FFAB2D !default;
$danger : #fd5353 !default;
$light : #dedeed !default;
$dark : #5b5e81;


:root {
  --primary: #{$primary};
  --secondary: #{$secondary};
  --primary-hover: #{darken($primary, 10)};
  --primary-dark: #{darken($primary, 30)};
  --rgba-primary-1: #{rgba($primary, 0.1)};
  --rgba-primary-2: #{rgba($primary, 0.2)};
  --rgba-primary-3: #{rgba($primary, 0.3)};
  --rgba-primary-4: #{rgba($primary, 0.4)};
  --rgba-primary-5: #{rgba($primary, 0.5)};
  --rgba-primary-6: #{rgba($primary, 0.6)};
  --rgba-primary-7: #{rgba($primary, 0.7)};
  --rgba-primary-8: #{rgba($primary, 0.8)};
  --rgba-primary-9: #{rgba($primary, 0.9)};
  --rgba-secondary-1: #{rgba($secondary, 0.1)};
  --rgba-secondary-2: #{rgba($secondary, 0.2)};
  --rgba-secondary-3: #{rgba($secondary, 0.3)};
  --rgba-secondary-4: #{rgba($secondary, 0.4)};
  --rgba-secondary-5: #{rgba($secondary, 0.5)};
  --rgba-secondary-6: #{rgba($secondary, 0.6)};
  --rgba-secondary-7: #{rgba($secondary, 0.7)};
  --rgba-secondary-8: #{rgba($secondary, 0.8)};
  --rgba-secondary-9: #{rgba($secondary, 0.9)};
  --font-family-base: #{$font-family-base};
  --font-family-title: #{$font-family-base};
  --font-family-base: #{$font-family-base};
  --font-family-title: #{$font-family-base};
  --title: #{$headings-color};
}




$theme-colors: (
  ) !default;
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(("primary": $primary,
      "secondary": $secondary,
      "success": $success,
      "info": $info,
      "warning": $warning,
      "danger": $danger,
      "light": $light,
      "dark": $dark),
    $theme-colors
);


// $base-font-size   : 0.8125rem !default;
$heading-primary-size: 1.8rem;
$sub-heading-size : 1.6rem !default;

$grid-gutter-width: 30px;

$dropdown-lik-color: $body-color;


$border-color : #EEEEEE;
$headings-color: #3d4465 !default;
$rounded : 1rem;

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1440
);

$border-radius: .25rem;
$border-radius-sm: .2rem;
$border-radius-lg: .3rem;
$border-radius-pill: 50rem;


// Form
$input-padding-x: 1.25rem;
$input-padding-y: 0.6875rem;
//$input-border-color
$input-line-height: 1.5;
$dropdown-item-padding-x: $input-padding-x;